
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import { getTimelineData } from '@/api/home'

export default {
name: 'Home',
components: { Timeline, TimelineItem, InlineLoading },
data () {
return {
showLoading: true,
timelineData: []
}
},
created () {
this.getTimelineData()
},
methods: {
getTimelineData () {
this.showLoading = true
getTimelineData().then(res => {
this.showLoading = false
this.timelineData = res.data
}).catch(res => {
this.showLoading = false
})
}
}
}
